import { Component } from '@angular/core';
import { AlertController, IonicPage, LoadingController, NavController, NavParams, Platform } from 'ionic-angular';
import { LoginProvider } from '../../providers/login/login';
import { ThreadProvider } from '../../providers/thread/thread';
import { ChatListPage } from '../chats/chat-list/chat-list';
import { HomePage } from '../home/home';
import { NotificationsListPage } from '../notifications/notifications-list/notifications-list';
import { ProfilePage } from '../profile/profile';

@IonicPage()
@Component({
  selector: 'page-routing',
  templateUrl: 'routing.html',
})
export class RoutingPage {

  constructor(public navCtrl: NavController,
    public navParams: NavParams,
    public loading: LoadingController,
    public atrCtrl: AlertController,
    private platform: Platform,
    private loginProvider: LoginProvider,
    private threadProvider: ThreadProvider) {
  }

  async openProfile() {
    const loading = this.loading.create({
      content: "Por favor, aguarde...",
    });

    loading.present();

    let id = localStorage.getItem('userId');
    console.log(id);

    if (!id) {
      this.navCtrl.popToRoot();
    }

    this.loginProvider.userData(id).subscribe(async data => {

      this.navCtrl.push(ProfilePage, { user: data });

      await loading.dismiss();
    }, async error => {
      console.log(error);

      await loading.dismiss();
    });
  }

  async openNotifications() {
    this.navCtrl.push(NotificationsListPage);
  }

  async openChats() {
    const loading = this.loading.create({
      content: "Por favor, aguarde...",
    });

    loading.present();

    let id = localStorage.getItem('userId');
    console.log(id);

    if (!id) {
      this.navCtrl.popToRoot();
    }

		this.threadProvider.getThreads(id).subscribe(async data => {
			console.log(data);      
      this.navCtrl.push(ChatListPage, { threads: data });

      await loading.dismiss();
		}, async error => {

			console.log(error);
			await loading.dismiss();
		});
  }

  async logout() {

    let alert = this.atrCtrl.create({
      title: 'Aviso',
      subTitle: 'Deseja fazer logout do aplicativo?',
      buttons: [
        {
          text: 'Não',
          role: 'cancel',
          handler: () => {
            console.log('No clicked');
          }
        },
        {
          text: 'Sim',
          handler: () => {
            console.log('Yes clicked');

            localStorage.clear();

            this.navCtrl.push(HomePage);
          }
        }
      ]
    });

    alert.present();
  }

  async closeApp() {

    this.platform.exitApp();

  }

}



