import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, LoadingController } from 'ionic-angular';
import { User } from '../../../models/user';
import { Category } from '../../../models/category';
import { ConfirmRegisterPage } from '../confirm-register/confirm-register';
import { RegisterProvider } from '../../../providers/register/register';

@IonicPage()
@Component({
  selector: 'page-review-register',
  templateUrl: 'review-register.html',
})
export class ReviewRegisterPage {

  user: User;

  cnpj: string = '';
  tradingName: string = '';
  email: string = '';
  telephone: string = '';
  cellphone: string = '';
  site: string = '';
  responsiblePerson: string = '';
  categories: Category[];

  companyName: string = '';
  address: string = '';
  numberAddress: string = '';
  complementAddress: string = '';
  zipCode: string = '';
  neighborhood: string = '';
  city: string = '';
  state: string = '';

  observations: string = '';
  hiddenSendError: boolean = true;
  hiddenCnpjError: boolean = true;

  constructor(public navCtrl: NavController, 
    public navParams: NavParams, 
    public loading: LoadingController, 
    private registerProvider: RegisterProvider) {
    this.user = navParams.get('user');

    this.instanceLabels();
  }

  instanceLabels() {
    this.cnpj = this.user.cnpj.cnpj;
    this.tradingName = this.user.tradingName;
    this.email = this.user.email;
    this.telephone = this.user.telephone;
    this.cellphone = this.user.cellphone;
    this.site = this.user.site;
    this.responsiblePerson = this.user.responsiblePerson;
    this.categories = this.user.categories;
    this.companyName = this.user.cnpj.nome;
    this.address = this.user.cnpj.logradouro;
    this.numberAddress = this.user.cnpj.numero;
    this.complementAddress = this.user.cnpj.complemento;
    this.zipCode = this.user.cnpj.cep;
    this.neighborhood = this.user.cnpj.bairro;
    this.city = this.user.cnpj.municipio;
    this.state = this.user.cnpj.uf;
  }

  async sendData() {
    const loading = this.loading.create({
      content: "Por favor, aguarde...",
    });
    await loading.present();

    this.registerProvider.sendUser(this.user).subscribe(async data => {  
      this.user.observations = this.observations;

      this.navCtrl.push(ConfirmRegisterPage);

      await loading.dismiss();
     }, async error => {
      console.log(error);

      if (error["status"] == 200){
        this.navCtrl.push(ConfirmRegisterPage);
      } else if (error["status"] == 400) {
        this.hiddenCnpjError = false;
      } else {
        this.hiddenSendError = false;
      }

      await loading.dismiss();
    });
  }

}



