import { Component } from '@angular/core';
import { ModalController } from 'ionic-angular';

@Component({
	template:
	`
	<ion-list no-padding no-margin>
		<ion-item color="light" class="text-10" (click)="block()">
			<ion-label>Bloquear</ion-label>
		</ion-item>

		<ion-item color="light" class="text-10" (click)="inform()">
			<ion-label>Denunciar</ion-label>
		</ion-item>

		<ion-item color="light" class="text-10" (click)="delete()">
			<ion-label>Excluir</ion-label>
		</ion-item>
	</ion-list>
	`
})

export class ChatPopoverComponent {
	constructor(public modalCtrl: ModalController) { }

	block() {
		console.log('block');
	}

	inform() {
		console.log('inform');
	}

	delete() {
		console.log('delete');
	}
}



