import { CnpjObject } from '../models/cnpj-object';
import { Category } from '../models/category';

export class User {

    id: string;
    cnpj: CnpjObject;
    tradingName: string;
    email: string;
    telephone: string;
    coupon: string;
    conditionsTerms: boolean;
    site: string;
    cellphone: string;
    responsiblePerson: string;
    categories: Category[];
    observations: string;
    about: string;
    cep: string;
    number: any;
    complement: string;
    address: string;
    neighbourhood: string;
    city: string;
    state: string;
}


