import { BrowserModule } from '@angular/platform-browser';
import { ErrorHandler, NgModule } from '@angular/core';
import { IonicApp, IonicErrorHandler, IonicModule } from 'ionic-angular';
import { SplashScreen } from '@ionic-native/splash-screen';
import { StatusBar } from '@ionic-native/status-bar';
import { HttpClientModule } from '@angular/common/http';
import { Crop } from '@ionic-native/crop/ngx';
import { ImagePicker } from '@ionic-native/image-picker';
import { File } from '@ionic-native/file';

import { MyApp } from './app.component';
import { HomePage } from '../pages/home/home';

import { NotificationProvider } from '../providers/notification/notification';
import { RegisterProvider } from '../providers/register/register';
import { LoginProvider } from '../providers/login/login';

import { NotificationModel } from '../models/notification-model';
import { CnpjObject } from '../models/cnpj-object';
import { Category } from '../models/category';
import { Login } from '../models/login';
import { User } from '../models/user';

import { ConfirmRegisterPageModule } from '../pages/register-profile/confirm-register/confirm-register.module';
import { ReviewRegisterPageModule } from '../pages/register-profile/review-register/review-register.module';
import { Register_2PageModule } from '../pages/register-profile/register-2/register-2.module';
import { Register_1PageModule } from '../pages/register-profile/register-1/register-1.module';
import { NotificationsListPageModule } from '../pages/notifications/notifications-list/notifications-list.module';
import { NotificationDetailsPageModule } from '../pages/notifications/notification-details/notification-details.module';
import { ProfilePageModule } from '../pages/profile/profile.module';
import { RoutingPageModule } from '../pages/routing/routing.module';
import { LoginPageModule } from '../pages/login/login.module';
import { ChatListPageModule } from '../pages/chats/chat-list/chat-list.module';
import { ChatPageModule } from '../pages/chats/chat/chat.module';
import { ChatPopoverComponent } from '../pages/chats/chat/chat-popover.component';
import { ThreadProvider } from '../providers/thread/thread';

@NgModule({
  declarations: [
    MyApp,
    HomePage
  ],
  imports: [
    BrowserModule,
    HttpClientModule,
    IonicModule.forRoot(MyApp),
    Register_1PageModule,
    Register_2PageModule,
    ReviewRegisterPageModule,
    ConfirmRegisterPageModule,
    ProfilePageModule,
    LoginPageModule,
    RoutingPageModule,
    NotificationsListPageModule,
    NotificationDetailsPageModule,
    ChatListPageModule,
    ChatPageModule
  ],
  bootstrap: [IonicApp],
  entryComponents: [
    MyApp,
    HomePage
  ],
  providers: [
    StatusBar,
    SplashScreen,
    Crop,
    // ImagePicker,
    // File,
    { provide: ErrorHandler, useClass: IonicErrorHandler },
    RegisterProvider,
    LoginProvider,
    NotificationProvider,
    NotificationModel,
    CnpjObject,
    Category,
    Login,
    User,
    ThreadProvider,
    ChatPopoverComponent
  ]
})
export class AppModule { }



