import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, LoadingController } from 'ionic-angular';
import { RegisterProvider } from '../../../providers/register/register';
import { ReviewRegisterPage } from '../review-register/review-register';
import { User } from '../../../models/user';
import { Category } from '../../../models/category';

@IonicPage()
@Component({
  selector: 'page-register-2',
  templateUrl: 'register-2.html',
})
export class Register_2Page {

  user: User;

  site: string = '';
  cellphone: string = '';
  responsiblePerson: string = '';
  categories: Category[];
  selectedCategories: Category[];
  categorySelect: Category;

  hiddenResponsiblePerson: boolean = true;
  hiddenSelectedCategories: boolean = true;
  hasEmptyCategory: boolean = true;
  index: number;

  constructor(public navCtrl: NavController, public navParams: NavParams, public loading: LoadingController,
    private registerProvider: RegisterProvider) {
    this.user = navParams.get('user');
  }

  ionViewWillEnter() {
    this.getCategories();
  }

  async getCategories() {
    const loading = this.loading.create({
      content: "Por favor, aguarde...",
    });
    await loading.present();

    this.index = 0;
    do {
      this.registerProvider.getCategories()
        .subscribe(response => {
          this.categories = JSON.parse(response) as Category[]
        })

      this.index++;

      // await this.delay(3000);
    } while ((!this.categories || this.categories.length == 0) && this.index < 5);

    await loading.dismiss();
  }

  removeCategory(category: Category) {
    this.selectedCategories = this.selectedCategories.filter(obj => obj !== category);

    if (this.selectedCategories.length == 0) {
      this.hasEmptyCategory = true;
    }
  }

  addSelectedValue() {
    this.hasEmptyCategory = false;

    if (!this.selectedCategories) {
      this.selectedCategories = [this.categorySelect];
      return;
    }

    if (this.selectedCategories.length == 3) {
      return;
    }

    if (this.selectedCategories.length > 0) {
    }

    this.selectedCategories = this.selectedCategories.filter(obj => obj !== this.categorySelect);

    this.selectedCategories.push(this.categorySelect);

    console.log(this.categorySelect);
  }

  goToReviewRegisterPage() {
    if (!this.responsiblePerson) {
      this.hiddenResponsiblePerson = false;
      return;
    } else {
      this.hiddenResponsiblePerson = true;
    }

    if (this.selectedCategories == null || this.selectedCategories.length == 0) {
      this.hiddenSelectedCategories = false;
      return;
    } else {
      this.hiddenSelectedCategories = true;
    }

    if (this.site) {
      this.user.site = "https://" + this.site.toLowerCase();
    }
    
    this.user.cellphone = this.cellphone;
    this.user.responsiblePerson = this.responsiblePerson;
    this.user.categories = this.selectedCategories.filter(obj => obj.idcategoriafornecedor);

    this.navCtrl.push(ReviewRegisterPage, { user: this.user });
  }

  // async delay(ms: number) {
  //   await new Promise(resolve => setTimeout(() => resolve(), ms)).then(() => console.log("fired"));
  // }
}



