import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Observable } from 'rxjs';
import { CnpjObject } from '../../models/cnpj-object';
import { User } from '../../models/user';
import { Urls } from '../../utils/urls';

@Injectable()
export class RegisterProvider {

  constructor(private http: HttpClient) {
  }

  searchCnpj(cnpj: string): Observable<CnpjObject> {
    let headers = new HttpHeaders();
    headers.append('X-Requested-With', 'XMLHttpRequest');

    return this.http.get<CnpjObject>(Urls.urlApiCnpj + 'v1/cnpj/' + cnpj, { headers });
  }

  hasCNPJ(cnpj: string): Observable<boolean> {

    return this.http.get<boolean>(Urls.url5CoWebApi + "v1/serviceprovider/hascnpj/" + cnpj);
  }

  getCategories(): Observable<string> {

    return this.http.get<string>(Urls.url5CoPhp + "api/categorias/");
  }

  getCep(cep: string) {
		return this.http.get('https://viacep.com.br/ws/' + cep + '/json/');
	}

  sendUser(user: User) {
    let tradingName = '*';
    if (user.tradingName != null && user.tradingName != "" && user.tradingName != " ") {
      tradingName = user.tradingName
    }

    let body = {
      idusuario: 3,
      nomefantasia: tradingName,
      email: user.email,
      nomecontato: user.responsiblePerson,
      telefone: user.telephone,
      celular: user.cellphone,
      nome: user.cnpj.nome,
      endereco: user.cnpj.logradouro,
      numero: user.cnpj.numero,
      complemento: user.cnpj.complemento,
      bairro: user.cnpj.bairro,
      cidade: user.cnpj.municipio,
      estado: user.cnpj.uf,
      cnpj: user.cnpj.cnpj,
      cep: user.cnpj.cep,
      categoriafornecedor: user.categories.length >= 1 ? user.categories[0].idcategoriafornecedor : "",
      categoriafornecedor2: user.categories.length >= 2 ? user.categories[1].idcategoriafornecedor : null,
      categoriafornecedor3: user.categories.length == 3 ? user.categories[2].idcategoriafornecedor : null,
      site: user.site,
      cupom: user.coupon,
      observacao: user.observations,
    }

    let header = new HttpHeaders();
    header.append('Accept', '*/*');
    header.append('Content-Type', 'multipart/form-data');

    return this.http.post(Urls.url5CoPhp + "api/fornecedor/", body, { headers: header, observe: 'response' });
  }

  saveUserPhoto(bytes, userId: string, type: string) {
    var header = {
      "Content-Type": "image/" + type,
    };

    console.log(header)

    return this.http.post(Urls.urlFiles + "user/" + userId + '.' + type, bytes, { headers: header });
  }

  editUserPhoto(url: string, userId: string) {
    let body = {
      urlFoto: url
    }

    return this.http.put(Urls.url5CoWebApi + "v1/serviceprovider/mydata/edit/" + userId, body);
  }

  editUser(user: User) {
    let body = {
      nomeContato: user.responsiblePerson,
      site: user.site,
      telefone: user.telephone,
      celular: user.cellphone,
      sobre: user.about,
      cep: user.cep,
      numero: user.number,
      complemento: user.complement,
      endereco: user.address,
      bairro: user.neighbourhood,
      categorias: user.categories,
      ufs: [
        {
          uf: user.state,
          cidades: [user.city]
        }
      ]
    }

    console.log(body.categorias);

    return this.http.put(Urls.url5CoWebApi + "v1/serviceprovider/mydata/edit/" + user.id, body);
  }
}


