import { Component } from '@angular/core';
import { IonicPage, Loading, LoadingController, NavController, NavParams } from 'ionic-angular';
import { NotificationModel } from '../../../models/notification-model';

@IonicPage()
@Component({
  selector: 'page-notifications',
  templateUrl: 'notification-details.html',
})
export class NotificationDetailsPage {
  
  loading: Loading;

  title: string;
  description: string;
  details: string;
  
  constructor(
    public navCtrl: NavController, 
    public navParams: NavParams,
    public loadingController: LoadingController) {

      this.loading = this.loadingController.create({
        content: 'Carregando página...'
      });

      let notification = navParams.get('notification');

      this.instanceScreen(notification);
  }

  async instanceScreen(notification: NotificationModel) {
    await this.loading.present();

    this.title = notification.title;
		this.description = notification.description;
		this.details = notification.details.replace('\"', "'").replace('"', "'");

    await this.loading.dismiss();
  }

}



