import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { ThreadData } from '../../models/thread-data';
import { ThreadMessage } from '../../models/thread-message';
import { ThreadUser } from '../../models/thread-user';
import { Urls } from '../../utils/urls';

@Injectable()
export class ThreadProvider {

  constructor(public http: HttpClient) {
    
  }

	getThreads(userId: string): Observable<ThreadUser[]> {

		return this.http.get<ThreadUser[]>(Urls.url5CoWebApi + 'v1/thread/serviceprovider/' + userId);
	}

	createThread(userId: string, supplierId: string) {

		var body = {
			id: userId + supplierId,
			threadId: userId + supplierId,
			userId1: userId,
			userId2: supplierId
		}

		return this.http.post<ThreadUser>(Urls.urlThreads + 'threads', body);
	}

	getMessages(threadId: string, token: string): Observable<ThreadData> {

		return this.http.get<ThreadData>(Urls.urlThreads + 'threads/' + threadId + '/messages?pageSize=10&token=' + token);
	}

	createMessage(clientId: string, providerId: string, message: string): Observable<ThreadMessage> {

		var body = {
			from: providerId,
			to: clientId,
			body: message,
			contentType: "text"
		}

		return this.http.post<ThreadMessage>(Urls.urlThreads + 'threads/' + clientId + providerId, body);
	}

}



