import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, ToastController, LoadingController, Loading } from 'ionic-angular';
import { LoginProvider } from '../../providers/login/login';
import { RoutingPage } from '../routing/routing';

@IonicPage()
@Component({
  selector: 'login',
  templateUrl: 'login.html',
})
export class LoginPage
{

  loading: Loading;

  email: string = '';
  password: string = '';
  
  hasGenericError: boolean = false;
  hasInvalidCredentials: boolean = false;

  constructor(public navCtrl: NavController, 
    public navParams: NavParams, 
    public loadingController: LoadingController,
    private loginProvider: LoginProvider)
  {
  }

  async sendAuth() {
    let login = {
      email: this.email,
      password: this.password
    }

    this.loginProvider.auth(login).subscribe(async data => {
      console.log(data.body.toString());

      localStorage.setItem('userId', data.body.toString());

      this.navCtrl.push(RoutingPage);

      await this.loading.dismiss();
     }, async error => {
      console.log(error.message);
            
      if (error["status"] == 401) {
        this.hasInvalidCredentials = true;
      } else {
        this.hasGenericError = true;
      }

      await this.loading.dismiss();
    });
  }

  async logMeIn(keyCode) {
    if (keyCode && keyCode != 13) {
      return;
    }

    this.loading = this.loadingController.create({
      content: 'Por favor, aguarde...'
    });

    await this.loading.present();

    if (!this.email || !this.password) {
      this.hasInvalidCredentials = true;
      await this.loading.dismiss();
      
      return;
    }
    else {
      this.hasInvalidCredentials = false;
    }

    await this.sendAuth();
  }

  async gotoNextField(nextElement, keyCode) {
    if (keyCode && keyCode != 13) {
      return;
    }

    nextElement.setFocus();
  }
}



