export class CnpjObject {
  atividade_principal: Atividade[];
  data_situacao: string;
  complemento: string;
  tipo: string;
  nome: string;
  telefone: string;
  email: string;
  atividade_secundaria: Atividade[];
  qsa: QSA[];
  situacao: string;
  bairro: string;
  logradouro: string;
  numero: string;
  cep: string;
  municipio: string;
  porte: string;
  abertura: string;
  natureza_juridica: string;
  uf: string;
  cnpj: string;
  ultima_atualizacao: string;
  status: string;
  fantasia: string;
  efr: string;
  motivo_situacao: string;
  situacao_especial: string;
  data_situacao_especial: string;
  capital_social: string;
}

export class Atividade {
  text: string;
  code: string;
}

export class QSA {
  qual: string;
  nome: string;
}


