import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, ToastController, LoadingController } from 'ionic-angular';
import { RegisterProvider } from '../../../providers/register/register';
import { CnpjObject } from '../../../models/cnpj-object';
import { Register_2Page } from '../register-2/register-2';
import { User } from '../../../models/user';

@IonicPage()
@Component({
  selector: 'page-register-1',
  templateUrl: 'register-1.html',
})
export class Register_1Page {

  cnpjObject: CnpjObject;

  cnpj: string = '';
  tradingName: string = '';
  email: string = '';
  telephone: string = '';
  coupon: string = '';
  conditionsTerms: boolean = false;

  hasCnpjError: boolean = false;
  hasCnpjEmpty: boolean = false;
  hasCnpjExists: boolean = false;
  hasEmailEmpty: boolean = false;
  hasTelephoneEmpty: boolean = false;
  hasConditionsTermsEmpty: boolean = false;

  index: number;

  constructor(public navCtrl: NavController, public navParams: NavParams, public loading: LoadingController,
    private registerProvider: RegisterProvider, private toastController: ToastController) {
  }

  async presentToast(message: string) {
    const toast = this.toastController.create({
      message: message,
      duration: 2000
    });
    toast.present();
  }

  async searchCnpj() {
    const loading = this.loading.create({
      content: "Por favor, aguarde...",
    });
    await loading.present();

    this.registerProvider.hasCNPJ(this.cnpj).subscribe(async data => {

      if (data) {

        this.hasCnpjExists = true;
        await loading.dismiss();

      } else {
        this.index = 0;
        do {
          this.registerProvider.searchCnpj(this.cnpj)
            .subscribe(response => {
              this.cnpjObject = response;
            })

          if (this.cnpjObject)
            break;

          this.index++;

          await this.delay(3000);
        } while (this.index < 3);

        await loading.dismiss();

        if (!this.cnpjObject) {
          this.email = '';
          this.telephone = '';
          this.tradingName = '';

          this.hasCnpjError = true;
          return;
        }

        this.hasCnpjExists = false;
        this.hasCnpjError = false;
        this.tradingName = this.cnpjObject.fantasia;
        this.email = this.cnpjObject.email;
        this.telephone = this.cnpjObject.telefone;

      }

    }, async error => {
      console.log(error.message);
    });
    
  }

  goToSecondStep() {
    if (!this.cnpj) {
      this.hasCnpjEmpty = true;
      return;
    } else {
      this.hasCnpjEmpty = false;
    }

    if (!this.email) {
      this.hasEmailEmpty = true;
      return;
    } else {
      this.hasEmailEmpty = false;
    }

    if (!this.telephone) {
      this.hasTelephoneEmpty = true;
      return;
    } else {
      this.hasTelephoneEmpty = false;
    }

    this.hasConditionsTermsEmpty = !this.conditionsTerms

    if (this.hasConditionsTermsEmpty)
      return;

    if (this.cnpjObject == null) {
      this.hasCnpjError = true;
      return;
    }

    var user = new User();
    user.cnpj = this.cnpjObject;
    user.tradingName = this.tradingName;
    user.email = this.email.toLowerCase();
    user.telephone = this.telephone;
    user.coupon = this.coupon;
    user.conditionsTerms = this.conditionsTerms;

    this.navCtrl.push(Register_2Page, { user: user });
  }

  async delay(ms: number) {
    await new Promise(resolve => setTimeout(() => resolve(), ms)).then(() => console.log("fired"));
  }
}



