import { Component } from '@angular/core';
import { IonicPage, Loading, LoadingController, NavController, NavParams } from 'ionic-angular';
import { ThreadUser } from '../../../models/thread-user';
import { ChatPage } from '../chat/chat';

@IonicPage()
@Component({
  selector: 'page-chat-list',
  templateUrl: 'chat-list.html',
})
export class ChatListPage {

  loading: Loading;
  registerUrl: string;
  threads: ThreadUser[];
  hasLoaded = false;

  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    private loadingCtrl: LoadingController) {

      this.instancePage();
  }

  async instancePage() {
    this.loading = this.loadingCtrl.create({
      content: 'Carregando chats...'
    });

    this.loading.present();

    this.threads = this.navParams.get('threads');

    if (!this.threads || this.threads.length == 0) {
      await this.loading.dismiss();

      this.hasLoaded = true;

      return;
    }

    this.threads.forEach(async (t: ThreadUser) => {
      t.providerName = t.providerName.toUpperCase();
      t.providerPhoto = (!t.providerPhoto || t.providerPhoto.trim() == '') ? '../../assets/imgs/default-profile-img.jpg' : t.providerPhoto;      
      
      this.hasLoaded = true;
    });

    await this.loading.dismiss();
  }

  async openDetails(thread: ThreadUser) {
    this.navCtrl.push(ChatPage, { thread: thread });
  }

}



