import { Component, ElementRef, ViewChild } from '@angular/core';
import { AlertController, InfiniteScroll, IonicPage, Loading, LoadingController, NavController, NavParams, PopoverController } from 'ionic-angular';
import { ThreadMessage } from '../../../models/thread-message';
import { ThreadUser } from '../../../models/thread-user';
import { ThreadProvider } from '../../../providers/thread/thread';
import { ChatPopoverComponent } from './chat-popover.component';
import { interval } from 'rxjs/observable/interval';
import { Dictionary } from '../../../utils/dictionary';

@IonicPage()
@Component({
  selector: 'page-chat',
  templateUrl: 'chat.html',
})
export class ChatPage {

  @ViewChild('infiniteScroll') infiniteScroll: InfiniteScroll;
	@ViewChild('content') content: any;

	// messages: ThreadMessageModel[];
	messages: Dictionary<Array<ThreadMessage>>;
	normalMessages: ThreadMessage[];
	thread: ThreadUser;
	currentToken: string;
	userMessage: string;
	userId: string;

	constructor(
		public popoverCtrl: PopoverController,
		public alertCtrl: AlertController,
		public loading: LoadingController,
		public navParams: NavParams,
		private provider: ThreadProvider) {

		this.userId = localStorage.getItem('userId');
		this.thread = navParams.get('thread');
		this.userMessage = "";

		this.instanceScreen();
	}

	ngOnInit() {
		localStorage.setItem('isModal', 'true');

		this.messages = new Dictionary<Array<ThreadMessage>>();
	}

	async instanceScreen() {
		const loading = await this.loading.create({
			content: "Carregando conversa...",
		});

		await loading.present();

		interval(3000).subscribe(
			() => {
				this.provider.getMessages(this.thread.id, "").subscribe(async data => {
					console.log(data);
		
					this.normalMessages = data.data.reverse();
					this.currentToken = data.token;
    
          this.messages = new Dictionary<Array<ThreadMessage>>();
					this.normalMessages.forEach(m => {
						var dt = new Date(m.createdAt); 
		
						var day = dt.getDate(),
						month = dt.getMonth(),
						year = dt.getFullYear(),
						hours = dt.getHours(),
						minutes = dt.getMinutes();
		
						m.convertDate = day + '/' + (month + 1) + '/' + year;
						m.convertTime = hours + ':' + minutes;

						if (this.messages.containsKey(m.convertDate)) {
							var list  = this.messages.get(m.convertDate);
		
							list.push(m);
							this.messages[m.convertDate] = list;
						}
						else {
							this.messages.add(m.convertDate, [m]);
						}
					});		
				}, async error => {
		
					console.log(error);
					await loading.dismiss();
				});
			}
		);

		this.provider.getMessages(this.thread.id, "").subscribe(async data => {
			console.log(data);

			this.normalMessages = data.data.reverse();
			this.currentToken = data.token;

			this.normalMessages.forEach(m => {
				var dt = new Date(m.createdAt); 

				var day = dt.getDate(),
				month = dt.getMonth(),
				year = dt.getFullYear(),
				hours = dt.getHours(),
				minutes = dt.getMinutes();

				m.convertDate = day + '/' + (month + 1) + '/' + year;
				m.convertTime = hours + ':' + minutes;

				if (this.messages.containsKey(m.convertDate)) {
					var list  = this.messages.get(m.convertDate);

					list.push(m);
					this.messages[m.convertDate] = list;
				}
				else {
					this.messages.add(m.convertDate, [m]);
				}
			});

			setTimeout(() => this.content.scrollToBottom(0), 500);

			setTimeout(async () => await loading.dismiss(), 500);

			console.log(this.messages);

		}, async error => {

			console.log(error);
			await loading.dismiss();
		});
	}

	async sendMessage() {
		this.userMessage = this.userMessage.trim();

		if (!this.userMessage || this.userMessage == "") {
			return;
		}

		this.provider.createMessage(this.thread.userId1, this.thread.userId2, this.userMessage).subscribe(async data => {

			console.log(data);

			this.normalMessages.push(data);

			var dt = new Date(data.createdAt); 

			var day = dt.getDate(),
			month = dt.getMonth(),
			year = dt.getFullYear(),
			hours = dt.getHours(),
			minutes = dt.getMinutes();

			data.convertDate = day + '/' + (month + 1) + '/' + year;
			data.convertTime = hours + ':' + minutes;

			if (this.messages.containsKey(data.convertDate)) {
				var list  = this.messages.get(data.convertDate);

				list.push(data);
				this.messages[data.convertDate] = list;
			}
			else {
				this.messages.add(data.convertDate, [data]);
			}

			this.userMessage = "";
		}, async error => {

			console.log(error);

			let alert = await this.alertCtrl.create({ message: 'Ocorreu um erro ao enviar a mensagem', buttons: [] });
			await alert.present();
		});
	}

	async settingsPopover(ev: any) {
		const siteInfo = { id: 1, name: 'edupala' };

		const popover = await this.popoverCtrl.create({
			component: ChatPopoverComponent,
			event: ev,
			cssClass: 'popover_setting',
			componentProps: {
				site: siteInfo
			},
			translucent: true
		});

		return await popover.present();
	}

	async loadData(event) {
		if (!this.currentToken) {
			return;
		}

		this.messages = null;
		this.provider.getMessages(this.thread.id, this.currentToken).subscribe(async data => {
			console.log(data);

			this.normalMessages = this.normalMessages.reverse();

			this.normalMessages = this.normalMessages.concat(data.data);
			this.currentToken = data.token;

			this.normalMessages = this.normalMessages.reverse();

			this.normalMessages.forEach(m => {
				var dt = new Date(m.createdAt); 

				var day = dt.getDate(),
				month = dt.getMonth(),
				year = dt.getFullYear(),
				hours = dt.getHours(),
				minutes = dt.getMinutes();

				m.convertDate = day + '/' + (month + 1) + '/' + year;
				m.convertTime = hours + ':' + minutes;

				if (this.messages.containsKey(m.convertDate)) {
					var list  = this.messages.get(m.convertDate);

					list.push(m);
					this.messages[m.convertDate] = list;
				}
				else {
					this.messages.add(m.convertDate, [m]);
				}
			});
		}, async error => {

			console.log(error);
			event.target.complete();
		});
	}
}


