import { Component } from '@angular/core';
import { IonicPage, Loading, LoadingController, NavController, NavParams } from 'ionic-angular';
import { NotificationModel } from '../../../models/notification-model';
import { NotificationProvider } from '../../../providers/notification/notification';
import { NotificationDetailsPage } from '../notification-details/notification-details';

@IonicPage()
@Component({
  selector: 'page-notifications',
  templateUrl: 'notifications-list.html',
})
export class NotificationsListPage {

  loading: Loading;

  userId: string;
  notifications: NotificationModel[];

  constructor(
    public navCtrl: NavController, 
    public navParams: NavParams,
    private loadingController: LoadingController,
    private provider: NotificationProvider) {

      this.userId = localStorage.getItem('userId');

      if (this.userId == null) {
        this.navCtrl.popToRoot();
        return;
      }

      this.instanceScreen();
  }

  instanceScreen() {
    this.loading = this.loadingController.create({
      content: 'Carregando notificações...'
    });

    this.provider.getNotifications().subscribe(async data => {
      console.log(data);

      this.notifications = data;

      this.loading.dismiss();
     }, 
     async error => {
      console.log(error.message);

      this.loading.dismiss();
    });
  }

  async openDetails(notification: NotificationModel) {
    console.log(notification);

    this.navCtrl.push(NotificationDetailsPage, { notification: notification });
  }

}



